body {
  height: 100%;
  background: #f7f7f7; }

a {
  -webkit-transition: all 0.5s ease;
  -moz-transition: all 0.5s ease;
  -ms-transition: all 0.5s ease;
  -o-transition: all 0.5s ease;
  transition: all 0.5s ease; }

h1 {
  font-size: 36px;
  font-size: 2.25rem;
  font-weight: 300;
  margin-bottom: 30px; }

h2 {
  font-size: 16px;
  font-size: 1rem; }

h3 {
  font-size: 14px;
  font-size: 0.875rem;
  color: #333; }

label {
  font-weight: 500; }

.label a {
  color: #FFF; }
  .label a:hover {
    color: #ccc; }

.top-header {
  margin-top: 0; }

.back-button-container {
  margin-bottom: 20px; }

.pagination-container {
  min-height: 70px; }

.form-group .error {
  color: #a94442;
  font-size: 0.9em; }

.large-notice {
  padding: 60px 0;
  font-size: 1.2em;
  text-align: center; }

.alert ul {
  padding: 0; }
  .alert ul li {
    list-style: none; }

.recommend-a-friend {
  border: 1px solid #ccc;
  padding: 20px;
  margin-top: 20px; }
  .recommend-a-friend h2, .recommend-a-friend h3 {
    margin-top: 0; }

.map-canvas {
  width: 100%;
  height: 400px;
  position: relative;
  background-color: #e5e3df; }

.navbar-brand {
  padding: 4px 15px;
  display: block; }
  .navbar-brand img {
    max-height: 100%; }

footer {
  padding: 40px 0;
  background: #f0f0f0; }

