.social-share {
  padding: 0;
  margin: 0; }
  .social-share li {
    list-style: none;
    float: left; }
    .social-share li a {
      display: block;
      width: 32px;
      margin-right: 6px; }

.image-container {
  display: block;
  position: relative;
  width: 100%; }
  .image-container .property-slide-show, .image-container .property-single-image {
    padding: 0;
    margin: 0;
    font-size: 24px;
    font-size: 1.5rem; }
  .image-container .property-single-image img {
    width: 100%; }
  .image-container .property-slide-show a.image-link, .image-container .property-slide-show a.fancybox {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 11;
    list-style: none; }
    .image-container .property-slide-show a.image-link:not(:first-child) img, .image-container .property-slide-show a.fancybox:not(:first-child) img {
      display: none; }
  .image-container .nivo-directionNav {
    padding: 0; }
    .image-container .nivo-directionNav a {
      display: block;
      width: 18px;
      height: 18px;
      text-align: center;
      color: #333;
      background: rgba(255, 255, 255, 0.6);
      border-radius: 50%;
      font-family: FontAwesome; }
      .image-container .nivo-directionNav a.nivo-prevNav {
        margin-left: 20px; }
        .image-container .nivo-directionNav a.nivo-prevNav:before {
          content: "\f137"; }
      .image-container .nivo-directionNav a.nivo-nextNav {
        margin-right: 20px; }
        .image-container .nivo-directionNav a.nivo-nextNav:before {
          content: "\f138"; }
      .image-container .nivo-directionNav a:hover {
        text-decoration: none;
        color: #fff;
        background: rgba(0, 0, 0, 0.6); }
  .image-container .nivo-thumbs-enabled {
    text-align: left;
    padding: 0; }
    .image-container .nivo-thumbs-enabled a img {
      width: 50px; }

